.videoContainer {
    position: relative;    
    overflow: hidden;
    background-color: #eee;
    border-radius: 4px;
    border: 1px solid #ebebeb;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.errorContainer {
    text-align: center;
}

.errorIcon {
    color: #d30000;
}

.errorText {
    font-size: 14px;
}

.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 1;
    cursor: pointer;
}

.playIcon {
    color: #dcdcdc;
}

.userIcon {
    color: #dcdcdc;
}

.volumeOffIcon {
    color: #828282;
    position: absolute;
}



